.detail_bgimg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  opacity: 70%;
}

.detail_detail {
  display: flex;

}
.detail_info {
  margin-left: 30px;
}
.detail_info h2 {
    color: #fff;
}

.detail_info ul{
    padding: 0 0 0 20px;
    color: #fff;
    font-size: 18px;
}


